// --- Animations for a "Wow Experience" ---
const slideInUp = keyframes`
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
`;

// --- MODIFIED ---: A more subtle, breathing shadow effect for the idle state
const pulse = keyframes`
  0% { box-shadow: 0 6px 16px rgba(79, 70, 229, 0.3); }
  50% { box-shadow: 0 8px 24px rgba(79, 70, 229, 0.5); }
  100% { box-shadow: 0 6px 16px rgba(79, 70, 229, 0.3); }
`;

// --- MODIFIED ---: A slower, more graceful floating animation for the idle state
const float = keyframes`
  0% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
  100% { transform: translateY(0); }
`;

// --- NEW ---: A more energetic bounce animation for the hover state
const bounce = keyframes`
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
`;

const RippleEffect = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: ${props => props.theme.accentBright || '#4F46E5'};
  opacity: 0.1;
`;

const ButtonContent = styled.div`
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 28px;
  
  ${props => props.isOpen && `
    animation: none;
    transform: rotate(0);
  `}
`;

// --- MODIFIED ---: Updated FloatingButton for the best experience
const FloatingButton = styled.button`
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background: linear-gradient(135deg, #4F46E5, #7C3AED);
  color: white;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  overflow: visible; /* Allows the shadow to be seen outside the button bounds */

  /* Default idle animation */
  animation: ${float} 4s ease-in-out infinite;

  /* The shadow is now the primary pulsing element */
  box-shadow: 0 6px 16px rgba(79, 70, 229, 0.3);
  animation: ${pulse} 2.5s ease-in-out infinite, ${float} 4s ease-in-out infinite;


  /* On hover, override the idle animation with the bounce */
  &:hover {
    /* Use 'none' to stop the default animations and apply the bounce */
    animation: ${bounce} 0.8s ease;
    /* Enhance the shadow even more on hover for a lifting effect */
    box-shadow: 0 12px 28px rgba(79, 70, 229, 0.6);
  }

  &:active {
    transform: scale(0.95);
    transition: transform 0.1s ease;
  }
`;

