const LiveCommit = () => {
  const [commit, setCommit] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const GITHUB_TOKEN = '';
    const REPO_OWNER = 'Laxman824';
    const REPO_NAME = 'AWS-Cloud-technical-essentials';

    fetch(`https://api.github.com/repos/${REPO_OWNER}/${REPO_NAME}/commits`, {
      headers: { 'Authorization': `token ${GITHUB_TOKEN}` }
    })
    .then(res => res.json())
    .then(data => {
      if (data && data[0]) {
        setCommit(data[0].commit);
      }
      setLoading(false);
    })
    .catch(() => setLoading(false));
  }, []);

  if (loading) return 'Fetching live data from GitHub...';
  if (!commit) return 'Could not fetch the latest data. Please check my GitHub directly!';
  
  return (
    <div style={{fontFamily: 'inherit'}}>
      My latest work is on: <br />
      <strong style={{color: '#00284D'}}>{commit.message}</strong>
      <br/>
      <small>by {commit.author.name} on {new Date(commit.author.date).toLocaleDateString()}</small>
    </div>
  );
};


// --- Chatbot Steps Logic ---
const checkUserInput = ({ value }) => {
  const lowerCaseValue = value.toLowerCase();
  if (lowerCaseValue.includes('project')) return 'project_options';
  if (lowerCaseValue.includes('skill') || lowerCaseValue.includes('tech')) return 'skills';
  if (lowerCaseValue.includes('contact') || lowerCaseValue.includes('email')) return 'contact';
  if (lowerCaseValue.includes('education') || lowerCaseValue.includes('iit')) return 'education';
  if (lowerCaseValue.includes('creator') || lowerCaseValue.includes('who made you')) return 'creator_info';
  if (lowerCaseValue.includes('work') || lowerCaseValue.includes('commit')) return 'live_work';
  return 'unrecognized';
};

